* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    background: linear-gradient(135deg, #1e3c72 0%, #2a5298 100%);
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    color: #fff;
    overflow: hidden;
    height: 100vh;
}

.menu-container {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 720px;
    height: 480px;
    background: #2a2a2a;
    border-radius: 12px;
    box-shadow: 0 20px 60px rgba(0, 0, 0, 0.5);
    overflow: hidden;
    display: flex;
}

.sidebar {
    width: 200px;
    background: #1a1a1a;
    padding: 15px 0;
    border-right: 1px solid #333;
}

.logo {
    padding: 0 15px 20px 15px;
    border-bottom: 1px solid #333;
    margin-bottom: 15px;
}

.logo h1 {
    font-size: 28px;
    font-weight: 700;
    color: #fff;
    letter-spacing: 2px;
}

.logo p {
    font-size: 10px;
    color: #666;
    margin-top: 3px;
}

.nav-item {
    display: flex;
    align-items: center;
    padding: 10px 15px;
    cursor: pointer;
    transition: all 0.3s ease;
    border-left: 3px solid transparent;
}

.nav-item:hover {
    background: #333;
    border-left-color: #4a90e2;
}

.nav-item.active {
    background: #333;
    border-left-color: #4a90e2;
}

.nav-text {
    font-size: 12px;
    font-weight: 500;
}

.main-content {
    flex: 1;
    padding: 20px;
    background: #242424;
    overflow-y: auto;
}

.content-section {
    display: none;
}

.content-section.active {
    display: block;
}

.section-title {
    font-size: 20px;
    font-weight: 600;
    margin-bottom: 20px;
    color: #fff;
}

.content-grid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
    height: 100%;
}

.content-panel {
    background: #1a1a1a;
    border-radius: 8px;
    padding: 15px;
    border: 1px solid #333;
}

.panel-title {
    font-size: 14px;
    font-weight: 600;
    margin-bottom: 15px;
    color: #fff;
    text-align: center;
}

.input-group {
    margin-bottom: 12px;
}

.input-label {
    display: block;
    font-size: 10px;
    color: #ccc;
    margin-bottom: 4px;
}

.input-field {
    width: 100%;
    padding: 6px 10px;
    background: #333;
    border: 1px solid #444;
    border-radius: 4px;
    color: #fff;
    font-size: 12px;
}

.input-field:focus {
    outline: none;
    border-color: #4a90e2;
}

.btn {
    width: 100%;
    padding: 8px;
    background: #4a90e2;
    border: none;
    border-radius: 4px;
    color: #fff;
    font-size: 12px;
    font-weight: 500;
    cursor: pointer;
    transition: all 0.3s ease;
    margin-bottom: 8px;
}

.btn:hover {
    background: #357abd;
    transform: translateY(-1px);
}

.btn-danger {
    background: #dc3545;
}

.btn-danger:hover {
    background: #c82333;
}

.btn-warning {
    background: #ffc107;
    color: #000;
}

.btn-warning:hover {
    background: #e0a800;
}

.btn-success {
    background: #28a745;
}

.btn-success:hover {
    background: #218838;
}

.action-grid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 8px;
}

.action-btn {
    padding: 8px 6px;
    background: #333;
    border: 1px solid #444;
    border-radius: 4px;
    color: #fff;
    font-size: 10px;
    cursor: pointer;
    transition: all 0.3s ease;
    text-align: center;
}

.action-btn:hover {
    background: #4a90e2;
    border-color: #4a90e2;
}

.server-actions {
    display: flex;
    flex-direction: column;
    gap: 8px;
}

.server-btn {
    padding: 12px;
    background: #333;
    border: 1px solid #444;
    border-radius: 4px;
    color: #fff;
    font-size: 12px;
    cursor: pointer;
    transition: all 0.3s ease;
    text-align: center;
    position: relative;
}

.server-btn:hover {
    background: #dc3545;
    border-color: #dc3545;
}

.server-btn.active {
    background: #4a90e2;
    border-color: #4a90e2;
}

.console {
    position: absolute;
    bottom: 15px;
    right: 15px;
    width: 280px;
    height: 140px;
    background: #1a1a1a;
    border: 1px solid #333;
    border-radius: 8px;
    padding: 12px;
    font-family: 'Courier New', monospace;
    font-size: 9px;
    color: #0f0;
    overflow-y: auto;
    z-index: 1000;
}

.console-title {
    color: #fff;
    margin-bottom: 8px;
    font-weight: bold;
    font-size: 10px;
}

.console-output {
    max-height: 100px;
    overflow-y: auto;
}

.notification {
    position: fixed;
    top: 15px;
    right: 15px;
    min-width: 250px;
    max-width: 320px;
    background: #2a2a2a;
    border: 1px solid #4a90e2;
    border-radius: 8px;
    padding: 12px;
    box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);
    z-index: 1001;
    animation: slideIn 0.3s ease-out;
    margin-bottom: 10px;
}

.notification::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 3px;
    background: #4a90e2;
    border-radius: 8px 8px 0 0;
}

.notification-title {
    font-size: 12px;
    font-weight: 600;
    color: #fff;
    margin-bottom: 6px;
}

.notification-description {
    font-size: 10px;
    color: #ccc;
    line-height: 1.4;
}

.notification-close {
    position: absolute;
    top: 6px;
    right: 6px;
    background: none;
    border: none;
    color: #666;
    font-size: 14px;
    cursor: pointer;
    padding: 2px;
    line-height: 1;
}

.notification-close:hover {
    color: #fff;
}

@keyframes slideIn {
    from {
        transform: translateX(100%);
        opacity: 0;
    }
    to {
        transform: translateX(0);
        opacity: 1;
    }
}

.slider-container {
    margin-bottom: 12px;
}

.slider-label {
    display: flex;
    justify-content: space-between;
    font-size: 10px;
    color: #ccc;
    margin-bottom: 4px;
}

.slider {
    width: 100%;
    height: 5px;
    border-radius: 3px;
    background: #333;
    outline: none;
    cursor: pointer;
    appearance: none;
}

.slider::-webkit-slider-thumb {
    appearance: none;
    width: 14px;
    height: 14px;
    border-radius: 50%;
    background: #4a90e2;
    cursor: pointer;
}

.slider::-moz-range-thumb {
    width: 14px;
    height: 14px;
    border-radius: 50%;
    background: #4a90e2;
    cursor: pointer;
    border: none;
}

.checkbox-container {
    display: flex;
    align-items: center;
    gap: 8px;
    margin-bottom: 12px;
    cursor: pointer;
}

.checkbox {
    width: 16px;
    height: 16px;
    border: 2px solid #444;
    border-radius: 3px;
    background: transparent;
    position: relative;
}

.checkbox.checked {
    background: #4a90e2;
    border-color: #4a90e2;
}

.checkbox.checked::after {
    content: 'âœ“';
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: white;
    font-size: 10px;
    font-weight: bold;
}

.checkbox-label {
    font-size: 12px;
    color: #ccc;
    user-select: none;
}

/* Scrollbar styling */
.console::-webkit-scrollbar {
    width: 4px;
}

.console::-webkit-scrollbar-track {
    background: #1a1a1a;
}

.console::-webkit-scrollbar-thumb {
    background: #333;
    border-radius: 2px;
}

.console::-webkit-scrollbar-thumb:hover {
    background: #444;
}

.main-content::-webkit-scrollbar {
    width: 6px;
}

.main-content::-webkit-scrollbar-track {
    background: #242424;
}

.main-content::-webkit-scrollbar-thumb {
    background: #333;
    border-radius: 3px;
}

.main-content::-webkit-scrollbar-thumb:hover {
    background: #444;
}

/* Player Options Styles */
.player-option-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 8px 0;
    border-bottom: 1px solid #333;
}

.player-option-item:last-child {
    border-bottom: none;
}

.option-label {
    font-size: 12px;
    color: #fff;
    font-weight: 400;
}

.toggle-switch {
    width: 40px;
    height: 20px;
    background: #333;
    border-radius: 10px;
    position: relative;
    cursor: pointer;
    transition: all 0.3s ease;
    border: 1px solid #444;
}

.toggle-switch.active {
    background: #4a90e2;
    border-color: #4a90e2;
}

.toggle-slider {
    width: 16px;
    height: 16px;
    background: #fff;
    border-radius: 50%;
    position: absolute;
    top: 1px;
    left: 2px;
    transition: all 0.3s ease;
}

.toggle-switch.active .toggle-slider {
    transform: translateX(18px);
}

